<template lang="html">
  <div class="">
    <h2>{{this.getHelpData[0].title}}</h2>
    <div class="help__wrapper">
      <div class="help__pic">
        <div class="help__pic--each" v-for="(data, i) in getHelpDataPic">
          <img :src="data" alt="">
        </div>
      </div>
      <p v-html="this.getHelpData[0].description"></p>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'getData'
  ],
  computed: {
    getHelpData () {
      const data = this.$store.state.stateSelectedHelpData

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    getHelpDataPic () {
      const data = this.$store.state.stateSelectedHelpData

      if (data == null) {
        return 0
      } else {
        return data[0].picture
      }
    },
  }
}
</script>

<style lang="css">
</style>



