<template lang="html">
  <div class="script-title">
    <div class="">
      <content-loader
        v-if="!handleLoader"
        :height="736"
        :width="1024"
        :speed="2"
        primaryColor="#f3f3f3"
        secondaryColor="#dcdcdc"
       >
       <rect x="36.8" y="48.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="73.8" y="125.83" rx="0" ry="0" width="0" height="0" />
       <rect x="119.8" y="142.83" rx="0" ry="0" width="0" height="0" />
       <rect x="37.8" y="72.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="37.8" y="96.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="37.8" y="120.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="37.8" y="145.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="37.8" y="170.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="37.8" y="195.83" rx="0" ry="0" width="368" height="19.95" />
       <rect x="433.8" y="46.83" rx="0" ry="0" width="554.54" height="354.33" />
      </content-loader>
      <div class="script" v-if="handleLoader">
        <div class="script__left">
          <HelpList :getData="getData" />
        </div>
        <div class="script__right">
          <HelpDescriptions :getData="getData" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HelpList from '../../components/help-page/HelpList'
import HelpDescriptions from '../../components/help-page/HelpDescriptions.vue'
import { ContentLoader } from 'vue-content-loader';

export default {
  components: {
    HelpList,
    HelpDescriptions,
    ContentLoader
  },
  created () {
    if (this.$store.state.DataAdditional.role == "coach") {
      this.$store.dispatch('GET_HELP_COACH');
    } else {
      this.$store.dispatch('GET_HELP');
    }
  },
  computed: {
    getData () {
      const data = this.$store.state.stateHelp

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    whichHelpListCLicked () {
      const data = this.$store.state.stateClickedHelpList

      if (data == null) {
        return "Booking Setting"
      } else {
        return data
      }
    },
    handleLoader () {
      const data = this.$store.state.stateHelp

      if (data == null) {
        return false
      } else {
        return true
      }
    },
  }
}
</script>

<style lang="scss">
  @import './Help.scss';
  @import '../coaching-script/coachingScript.scss';
</style>



