<template lang="html">
  <div class="script__unit-list">
    <h2>Dyned Live Help Centre</h2>
    <ul>
      <li
        v-for="(data, i) in listScripts"
        :key="i"
        @click="clickedHelpList(i, data.title)"
        :class="{'list--active': i == activeIndex}"
      >{{data.title}}</li>
    </ul>
  </div>
</template>

<script>


export default {
  components: {

  },
  props: [
    'certTab',
    'getData'
  ],
  data () {
    return {
      unitCert: null,
      activeIndex: 0,
      currentHelp: "Days Off"
    }
  },
  computed: {
    listScripts () {
      // const whichCert = this.certTab
      const data = this.getData.data
      const filteredData = [];
      data.filter((item) =>{
        var i = filteredData.findIndex(x => (x.title == item.title));
        if(i <= -1){
              filteredData.push(item);
        }
        return null;
      });

      return filteredData
    }
  },
  methods: {
    clickedHelpList (i, title) {
      this.activeIndex = i

      const whichTitle = title
      const groupTitle = this.getData.data.filter(x => x.title === title)
      // console.log(groupTitle);
      this.currentHelp = groupTitle
      this.$store.commit("muttSelectedHelpData", groupTitle)
    }
  }
}
</script>

<style lang="css">
</style>



